import { Component, h, Host } from '@stencil/core';

@Component({
  tag: 'mt-quadro-01',
  styleUrl: 'quadro-01.scss',
  scoped: true,
})
export class Quadro01 {
  render() {
    return (
      <Host class="mt-quadro-01">
        <div class="mt-quadro-01__title">
          <slot name="title"></slot>
        </div>

        <p class="mt-quadro-01__text">
          <slot name="text"></slot>
        </p>
      </Host>
    );
  }
}
