$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  border-left: 3px solid #455a5a;
  display: block;
}

.mt-quadro-01 {
  &__title {
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    font-size: 14px;
    padding: 2px 10px;
    background-color: #455a5a;
    display: inline-block;
  }

  &__text {
    color: #455a5a;
    font-family: 'Merriweather', serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    padding: 6px;
  }
}
